/* Har med denna del för att tillämpa detta på alla html-element */
* 
{
    margin:0;
    padding:0;
}

body {
    font-family: 'Roboto', sans-serif;
    background-color: #877676b7;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}
section {
    margin-bottom: 20px; 
    flex-direction: column;
    align-items: left;
}
.container {
    display: flex;
    flex-wrap: wrap;
  
}

.left-column,
.right-column {
    width: 100%;
    box-sizing: border-box;
    padding: 20px;
}

h1 {
    margin: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.column img {
    width: 15%;
    height: auto;
    display: block;
    margin: auto;
}

.Meny ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    overflow: hidden;
    background-color: rgb(9, 9, 9);
    
}

.Meny li {
    float: left;
}

.Meny li a {
    display: block;
    color: rgb(252, 252, 252);
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

.Meny li a:hover {
    background-color: rgb(113, 130, 214);
}

footer {
    background-color: black;
    color: white;
    text-align: center;
    height: 40px;
    width: 100%;
    margin: 0;
    padding: 0;
  
}

.active {
    border-bottom: 2px solid #d5d5d5;
}

/* För mindre skärmar */
@media (max-width: 500px) {
    .container {
        font-size: 15px;
        display: flex;
        flex-direction: column;
    }

    .right-column {
        width: 100%;
        box-sizing: border-box;
        padding: 20px;
        font-size: 13px;
        display: flex;
        flex-direction: column;
    }

    .column img {
        width: 70%;
        height: auto;
        display: block;
        margin: auto;
    }

    .Meny ul {
        width: 100%;
    }

    .Meny li {
        width: 100%;
    }
}  