.Meny ul{
    list-style-type: none; 
    padding: 0; 
    margin: 0; 
    overflow: hidden;
    background-color: rgb(9, 9, 9);
}
.Meny li{
    float:left;
}
.Meny li a{
    display:block;
    color:rgb(252, 252, 252);
    text-align:center;
    padding: 14px 16px;
    text-decoration:none;
}
.Meny li a:hover{
    background-color:rgb(113, 130, 214);

}
.navbar{
    width: 100%;
    background-color: #333;
}
.menu-container{
    display:flex;
    align-items:center;
    justify-content: start;
    padding:10px;
}
.nav-links{
    list-style: none;
    display:flex;
    align-items: center;
    margin-left: 20px;
}
.nav-link{
    text-decoration: none;
    color:white;
    margin-right: 15px;
}
.nav-link:hover,
.nav-link:focus{
    color:#61dafe;
    background-color: #555;
}
.nav-links li{
    margin:10px 0;
}